<template>
    <div class="sc-pie-chart">

    </div>
</template>
<script>
export default {
    name:'pie-chart',
    data(){
        return{
            pieOptions:{},
            pieChart:null
        }
    },
    methods:{
        init(){
            this.pieChart = this.$echarts.init(document.querySelector('.sc-pie-chart'));
            this.pieChart.setOption(this.pieOptions);
        },
        createColor(c1,c2){
            return new this.$echarts.graphic.LinearGradient(0, 0, 1, 1, [{
                        offset: 0,
                        color: c1
                    },
                    {
                        offset: 1,
                        color: c2
                    }
            ])
        },
        getData(){
            let data1 = [
                {value:10, name:''},
                {value:5, name:''},
                {value:15, name:''},
                {value:25, name:''},
                {value:20, name:''},
                {value:30, name:''},
            ]
            let linearColor = this.createColor('#3665E8','#3AADF2');
            let areaColor1 = this.createColor('rgba(58, 255, 236, 1)','rgba(54, 153, 232, 1)');
            let areaColor2 = this.createColor('rgba(58, 157, 255, 1)','rgba(44, 76, 242, 1)');
            this.pieOptions = {
                series:[{
                    type:'pie',
                    name:'',
                    color:[areaColor1,areaColor2],
                    center:['50%','50%'],
                    radius:['0%','70%'],
                    data:data1,
                    roseType:'area',
                    itemStyle:{
                        // borderColor:'#fff'
                    },
                    label:{
                        fontSize:14,
                        align:'left',
                        width:50,
                        // formatter:'{a|{b}}\n{d}%',
                        formatter:'{a|{b}}',
                        rich:{
                            a:{
                                color:'#fff',
                                fontSize:12,
                                height:25
                            },
                        },
                    },
                    labelLine:{
                        show:true,
                        length:15,
                        length2:0,
                        lineStyle:{
                            color:'rgba(58, 173, 242, 1)',
                            width:1
                        }
                    }
                }]
            }
            this.init();
        }
    },
    mounted(){
        let t = this;
        this.$nextTick(()=>{
            t.pieChart.resize();
        })
        this.getData();
    }
}
</script>